#work{
    margin: 0 auto;
    min-width: calc(100vh-4rem);
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding:0 1rem;
}

.workTitle{
    margin: 1rem 0;
    font-size: 3rem;
}

.workDesc{
    font-weight: 300;
    font-size: 1rem;   
}
.workImgs{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    width: 100vw;
    max-width: 65rem;

}

.workImg{
    object-fit: cover;
    height: 10rem;
    margin: 0.5rem;

}

.workBtn{
    margin:3rem 0 ;
    padding: 0.5rem 2.5rem;
    border: none;
    background: white;
    border-radius: 2rem;
    font-size: 1rem;
}

@media screen and (max-width:585px){
    .workImg{
        height:50vw;
        
    }
}

